import { DOCUMENT } from '@angular/common';
import {
  Component,
  Inject,
  Input,
  OnDestroy,
  OnInit,
} from '@angular/core';

import { Subscription, fromEvent } from 'rxjs';
import { map } from 'rxjs/operators';

import {
  NAV_ITEMS,
  NavItem,
} from '../../models/nav-item.model';
import { WINDOW } from '../../../../core/services/window.provider';
import { isMobileDevice } from '../../../../shared/utils/responsive.util';

export type NavbarTheme = 'light' | 'dark';

const NAVBAR_DARK_THEME_OFFSET_Y = 10;

@Component({
  selector: 'app-start-page-navbar',
  templateUrl: './start-page-navbar.component.html',
  styleUrls: [
    './start-page-navbar.component.css',
  ],
})
export class StartPageNavbarComponent implements OnInit, OnDestroy {
  @Input()
  scrollContainer: any;

  private scrollSubscription: Subscription;

  public _name = 'start-page-navbar';

  public theme: NavbarTheme = 'light';
  public navItems: NavItem[] = NAV_ITEMS;
  public mobileMenuOpened = false;
  public isMobileDevice: boolean = isMobileDevice();

  public get iconButtonColor(): string {
    return this.theme === 'light' ? 'white' : 'primary-dark';
  }

  public getLinkColor(isActive: boolean): string {
    if (isActive) {
      return 'green';
    }

    return this.theme === 'light' ? 'white' : 'dark';
  }

  constructor(
    @Inject(DOCUMENT) private document: Document,
    @Inject(WINDOW) private window: Window,
  ) {}

  public ngOnInit(): void {
    this.configureInitialTheme();
    this.handleScrolling();
  }

  public ngOnDestroy(): void {
    this.scrollSubscription.unsubscribe();
  }

  public onMobileMenuOpen(): void {
    this.mobileMenuOpened = true;
  }

  public onMobileMenuClose(): void {
    this.mobileMenuOpened = false;
  }

  private configureInitialTheme(): void {
    this.theme = this.getThemeForOffsetY(this.scrollContainer);
  }

  private handleScrolling(): void {
    this.scrollSubscription = fromEvent(this.scrollContainer, 'scroll')
      .pipe(
        map((event: any) => {
          return event.target.scrollTop;
        }),
      )
      .subscribe(offsetY => {
        this.theme = this.getThemeForOffsetY(offsetY);
      });
  }

  private getThemeForOffsetY(offsetY: number): NavbarTheme {
    return offsetY >= NAVBAR_DARK_THEME_OFFSET_Y ? 'dark' : 'light';
  }
}
